.header h1 {
    font-size: 35px;    
    font-weight: bold;
    margin-top: 25px;        
}

.header h2 {
    font-size: 24px;
    font-weight: lighter;
}

.header h3 {
    font-size: 18px;
    margin-top: 20px;    
}

.language-button {      
    padding: 0;            
    text-align: right;    
}

.language-button .btn {
    margin-bottom: 20px;     
    width: 100px;
}

.profile img{
    border-radius: 50%;    
    height: 180px;
    width: 180px; 
}

.profile .fab {
    font-size: 30px;
    padding: 10px 7px;
}

  
@media (min-width: 305px) and (max-width: 575.98px) {

    .header h1 {
        font-size: 20px;
    }

    .header h2 {
        font-size: 17px;
    }

    .header h3 {
        font-size: 15px;
    }

    .language-button .btn {
        font-size: 13px;
        width: 80px;
    }

    .profile .fab {
        font-size: 25px;
    }

}

@media (min-width: 576px) and (max-width: 767.98px) {

    .header h1 {
        font-size: 30px;
    }

}